.e-alert {
  padding       : 1rem 2.5rem 1rem 1.25rem;
  @extend %background;
  border-radius : 4px;
  position      : relative;
  letter-spacing: 0.2px;
  vertical-align: middle;
  font-size     : 0.9rem;
  font-family   : inherit;
  text-shadow   : none;
  @extend .shadow;

  &.snack {
    border-radius          : 20px;
    border-top-right-radius: 0;
  }

  & > .e-icon {
    font-size     : 1.20rem;
    vertical-align: middle;
    margin-right  : 5px;
  }

  & > .e-delete {
    right   : 0.5rem;
    top     : 0.5rem;
    position: absolute;
  }

  & > a:not(.e-btn) {
    text-decoration: underline;
  }
}

.alert-footer {
  @extend .note-footer;
  margin-top: 10px;
  background: transparent;
  padding   : 10px 0 0;
}